const Card = (data) => {

    const [click, set_click] = useState('')

    function tecno(params) {
        return params.map(element => <p key={element}>{element}</p>)
    }
    const redir = (url) => {
        if (url !== '') window.open(url, '_blank')
    }
    const handle_click = (event) => {
        //console.log(event.target.dataset.link)
        set_click(event.target.dataset.link)
    }
    useEffect(() => {
        redir(click)
    }, [click]);
    return (
        <SwiperSlide>
            <div key={data.name} id={data.name} className="card">
                <div className="card_title"><h4>{data.name}</h4></div>
                <img className='card_img' src={data.graph} alt="project" />
                <div className="card_contend">
                    <div className="card_description">
                        <p>{data.description}</p>
                        <div id="skills">
                            {tecno(data.tecnologis)}
                        </div>
                    </div>
                    <div className="card_datas">
                        <div className='card_data'>
                            <p className='card_data_higth'>{data.data.develop_time}</p>
                            <p className='commentary'>Develop Time</p>
                        </div>
                        <div className='card_data'>
                            <p className='card_data_higth'>{data.data.contribuidors}</p>
                            <p className='commentary'>Contribuidors</p>
                        </div>
                        <div className='card_data'>
                            <p className='card_data_higth'>{data.data.project_status}</p>
                            <p className='commentary'>Project Status</p>
                        </div>
                    </div>
                </div>
                <button data-link={data.link} onClick={handle_click}>See Now</button>
            </div>
        </SwiperSlide>
    )
}

function Cards(params = []) {
    return params.map(element => Card(element))
}

const Projects = () => {
    const [img_br,set_img_br] = useState('')
    const [change,set_change] = useState(false)
    useEffect(() => {
        const cards_img = document.querySelectorAll('.card_img')
        let options = {
            root: document.querySelector('#intro_contend_2'),
            rootMargin: '0px',
            threshold: 0.95
        }
        const callback = (entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    //console.log(entry.target.src)
                    set_img_br(entry.target.src)
                }
            });
        }

        let observer = new IntersectionObserver(callback, options);

        cards_img.forEach(element => observer.observe(element))
    }, [])
    useEffect(() =>{
        set_change(true)
        set_change(false)
    },[img_br])

    return (
        <section id="projects">
            <div className='f_row' style={{zIndex:'1'}}><h2>Class </h2><h2 className='function'>Projects</h2><h2 className="simbol">{':'}</h2></div>
            <h2 style={{zIndex:'1'}}>{'      def __init__(self,project)'}</h2>
            <div className='center'>
                <div id='intro_contend_2'>
                    <Swiper
                        effect={"coverflow"}
                        grabCursor={true}
                        centeredSlides={true}
                        slidesPerView={2}
                        loop={false}
                        coverflowEffect={{
                            rotate: 50,
                            stretch: 0,
                            depth: 100,
                            modifier: 1,
                            slideShadows: false,
                        }}
                        pagination={{ clickable: true }}
                        modules={[EffectCoverflow, Pagination]}
                        className="mySwiper swiper"
                    >
                        {Cards(data)}
                    </Swiper>
                </div>
            </div>
            <motion.div id='project_img_bg' animate={{opacity: change?[0,1]:1}} transition={{duration:0.5}} initial={{opacity:0}}>
                <img src={img_br} alt="" />
            </motion.div>
        </section>
    )
}
export default Projects